*{
    padding: 0px;
    margin: 0px;
    box-sizing: border-box;
}
.login{
    height: 90vh;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index:0;
    position: relative;
    background: url(../../../login1.png) no-repeat scroll center / cover;
}
.login::before{
    content: "";
    z-index:1;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.5);
}
.login .loginContent{
    z-index:2;
    width:33%;

    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    /* margin-right: 150px; */
    border:  1px solid rgb(0, 0, 0);
    /* font-size: 30px; */
    height: 60%;
    color: rgb(193, 193, 193);
    border-radius: 20px;
    box-shadow: 0px 0px 5px 1px rgb(31, 30, 30);
    background: none;
    padding: 15px;

}
/* form{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
} */
.loginContent h1{
    font-size: 55px;
    text-align: center;
  
}
 .loginContent label{
    font-size: 24px;
    margin-top: 20px;
}
.loginContent input{
    font-size: 20px;
    background: none;
    border:1px solid  black;
    outline:gray;
    margin-top: 20px;
    color:  rgb(193, 193, 193);
    box-shadow: 0px 0px 5px 2px rgb(32, 31, 31);
    border-radius: 20px;
    padding: 5px 10px;
}
.loginContent button{
    margin: 15px 0px 15px 150px;
    background: none;
    outline:gray ;
   
    border: 1px solid black;
    box-shadow: 0px 0px 5px 2px rgb(32, 31, 31);
    border-radius: 20px;
    text-align: center;
  
    padding:5px 20px ;
    
}
.loginContent button a{
    font-size: 21px;
    background: none;
    text-decoration: none;
    color:  rgb(193, 193, 193);
    transition: all 0.8s ease;
  
}
.loginContent button a:hover{
    color: rgb(231, 231, 231) ;
}
 .loginContent h2{
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 20px;
}
.loginContent h2 a{
    text-decoration: none;
    transition:  all 0.8s ease;
    color:  rgb(193, 193, 193);
}
.loginContent h2 a:hover{
    color: rgb(231, 231, 231) ;
    /* list-style:upper-latin; */
}