*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.about{
    width: 100%;

}
.about .bannerAbout{
    width: 100%;
    height: 80vh;
    z-index: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    color: white;
    position: relative;
    background: url(./AboutImage-3.jpg) no-repeat scroll center / cover;
}
.bannerAbout::before{
    content: "";
    position: absolute;
    top: 0;
    z-index: 1;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
}
.bannerContent{
    width: 60%;
    position: absolute;
    top:45%;
    z-index: 2;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

}
.bannerContent h1{
    font-size: 50px;
    text-align: center;
    margin-top: 10px;
}
.bannerContent p{
    font-size: 18px;
    text-align: center;
    margin-top: 10px;
}
.bannerContent a{
    font-size: 20px;
    text-decoration: none;
    padding: 9px 25px;
    margin-top: 15px;
    border-radius: 1px;
    color: white;
    background-color: rgb(171, 0, 0);
    /* box-shadow: 0px 0px 12px 2px rgb(161, 11, 11); */
}

